import axios from 'axios';

const apiKey = process.env.REACT_APP_APIKEY
const baseUrl = process.env.REACT_APP_BASEURL
const ytKey = process.env.REACT_APP_YTKEY

export const getMovieList = async() => {

    const movie = await axios.get(`${baseUrl}/movie/popular?page=1&api_key=${apiKey}`)
    return(movie.data.results)
}

export const getMovieList2 = async() => {

    const movie = (await axios.get(`${baseUrl}/movie/popular?page=2&api_key=${apiKey}`)).data.results
    const movie2 = (await axios.get(`${baseUrl}/movie/popular?page=3&api_key=${apiKey}`)).data.results
    return movie.concat(movie2)
    
}

export const getMovieList3 = async() => {

    const movie = (await axios.get(`${baseUrl}/movie/popular?page=4&api_key=${apiKey}`)).data.results
    const movie2 = (await axios.get(`${baseUrl}/movie/popular?page=5&api_key=${apiKey}`)).data.results
    return movie.concat(movie2)
    
}

export const searchMovie = async(q) => {
    const search = await axios.get(`${baseUrl}/search/movie?page=1&query=${q}&api_key=${apiKey}`)
    return search.data
}

export const searchMovie2 = async(q) => {
    const search = (await axios.get(`${baseUrl}/search/movie?page=2&query=${q}&api_key=${apiKey}`)).data.results
    const search2 = (await axios.get(`${baseUrl}/search/movie?page=3&query=${q}&api_key=${apiKey}`)).data.results
    return search.concat(search2)
}

