export const clips = async(q) => {
    const video = await axios.get(`${baseUrl}/movie/${q}/videos?api_key=${apiKey}`)
    // console.log(video.data.results.find(finder))
    return video.data.results.find((vid) => vid.name === 'Official Trailer').key
}
