import { Suspense, useEffect, useRef, useState } from 'react';
import {
  getMovieList, getMovieList2, getMovieList3, 
  searchMovie, searchMovie2 , searchMovie3,
  genreFilter, genreFilter2, genreFilter3, clips} from '../api'
  import React from 'react';
  import Navbar from './Navbar';
  import '../index.css'
  import { AiOutlineSearch, AiOutlineClose } from 'react-icons/ai'
  import { CgMore } from 'react-icons/cg';
  import Loader from './Loader';
  import Youtube from 'react-youtube'
  const Cards = React.lazy(() => import('./Cards'))

export const Page1 = () => {
  
  const [key, setKey] = useState(0)

  const [popularMovies, setPopularMovies] = useState([key])

  const [vids, setVids] = useState('')

  const [vidKey, setVidKey] = useState('')

  const movie = popularMovies[key]

  const [genre, setGenre] = useState([])

  const barRef = useRef(null)

  useEffect(() => {
    getMovieList()
      .then((results) => {
      setGenre(results[key].genre_ids)
      })
      .catch(err => console.error(err))
  }, [key])
  
  useEffect(() => {
    getMovieList()
      .then((results)=>{
      setPopularMovies(results)
    })
    .catch(err => console.error(err))
  }, [])

  useEffect(() => {
    clips(vidKey)
      .then((results)=> {
      setVids(results)
      })
      .catch(err =>  console.error(err))
  }, [key])

  const listGenres = 
    {
      28: "Action",
      12: "Adventure",
      16: "Animation",
      35: "Comedy",
      80: "Crime",
      99: "Documentary",
      18: "Drama",
      10751: "Family",
      14: "Fantasy",
      36: "History",
      27: "Horror",
      10402: "Music",
      9648: "Mystery",
      10749: "Romance",
      878: "Science Fiction",
      10770: "TV Movie",
      53: "Thriller",
      10752: "War",
      37: "Western"
    }
    
  
  const PopularMovieList = () => {
    
    if(popularMovies.length === 0){
    return (<h1 className='h-screen'>Sorry, no result found 🥺</h1>)
  }else

    return ( 
      popularMovies.map((movie, a) =>(
        <div id='wrapper' className='group Movie-wrapper' key={a} onClick={() => {setKey(a); pop(); setVidKey(movie?.id)}}>
           <Suspense fallback={<Loader />}>
            <Cards movie={movie} />
           </Suspense>
          </div>
        
        )))
      }

      const Genres = (props) => {
        let one = props.genre[0]
        let two = props.genre[1]
        let three = props.genre[2]
        let four = props.genre[3]
        let wahid = listGenres[one]
        let itsnain = listGenres[two]
        let tsalasa = listGenres[three]
        let arbaa = listGenres[four]

        return (
          <>
          <p className='text-slate-300 text-sm'>{wahid}, {itsnain}, {tsalasa}, {arbaa}</p>
          </>
        )
      }

      const genrey = async(q) => {
        const yes = await genreFilter(q)
        let name = listGenres[q]
        setNum(1)
        setRe(q)
        setPopularMovies(yes)
        title.innerHTML = `Genre: ${name}`
      }

      const genrey2 = async(q, a) => {
        const yes = await genreFilter2(q)
        let name = listGenres[q]
        numPlus(a)
        setPopularMovies(yes)
        title.innerHTML = `Genre: ${name}`
     }

      const genrey3 = async(q, a) => {
        const yes = await genreFilter3(q)
        let name = listGenres[q]
        numPlus(a)
        setPopularMovies(yes)
        title.innerHTML = `Genre: ${name}`
     }

      var details = document.getElementById('details')
      let modal = document.getElementById('modal')
      let title = document.getElementById('title')
      let modmod = document.getElementById('usure')

      const pop = () => {
        details.showModal()
      }
      
      const usure = () => {
        modmod.showModal()
      }
      const sure = () => {
        modmod.close()
      }
      const unPop = () => {
        details.close()
      }
      
      const dialog = () => {
        modal.showModal()
      }
      
      const unDialog = () => {
        modal.close()
      }
      
      const prevB = (a) => {
        numPlus(a)
        getMovieList().then((results) => {
          setPopularMovies(results)
        })
      }
    
      const searchPress = async(q) => {
        q?.preventDefault()
        const query = await searchMovie(barRef.current.value)
        setNum(1)
        setPopularMovies(query.results)
        title.innerHTML = `Search: ${barRef.current.value}`
      }
      
      const searchPress2 = async(q, a) => {   
        const query = await searchMovie2(q)
        numPlus(a)
        setPopularMovies(query)
      }
      
      const searchPress3 = async(q, a) => {   
        const query = await searchMovie3(q)
        numPlus(a)
        setPopularMovies(query)
      }

      const changePage = (a) => {
        numPlus(a)
        getMovieList2().then((results) => {
          setPopularMovies(results)
        })
      }
      
      const changePage2 = (a) => {
        numPlus(a)
        getMovieList3().then((results) => {
          setPopularMovies(results)
        })
      }
    
     const NavItem = (props) => {
       const [open, setOpen] = useState(false)
       return(
         <div className="relative ml-3">
         <div>
             <button type="button" class="flex rounded-full bg-gray-800 text-sm t-2" id="user-menu-button" aria-expanded="false" aria-haspopup="true" onClick={()=> setOpen(!open)}>
             <span className="sr-only">Open user menu</span>
             <img className="h-8 w-8 rounded-full" src="https://placehold.co/40x40" alt="non"/>
             </button>
         </div>
           { open && props.children }
         </div>
       )
      }

      const GenreChoose = (props) => {
        const [open, setOpen] = useState(false)
        return(
          <> 
            <button type="button" onClick={() => setOpen(!open)} class="rounded-full bg-gray-800 p-1 text-gray-400 hover:text-white">
            <span class="sr-only">View notifications</span>
            <CgMore />
            </button>
            {open && props.children}
          </>
        )
      }
      const current = barRef.current?.value
      const [re, setRe] = useState(0)
      const [num, setNum] = useState(1)
      const numPlus = (q) => setNum(num+q)
       
      const prevButton = () => {
        re != 0 && num == 3 ? genrey2(re, -1) :
        re != 0 && num == 2 ? genrey(re, -1) :
        re != 0 && num == 1 ? genrey(re, 0) :
        current === '' && num == 3 ? changePage(-1) :
        current === '' && num == 2 ? prevB(-1) :
        current === '' && num == 1 ? prevB(0) :
        num == 3 ? searchPress3(current, -1) :
        num == 2 ? searchPress2(current, -1) :
        num == 1 ? searchPress(current, 0) : prevB()
      }
      const nextButton = () => {
        re != 0 && num == 1 ? genrey2(re, 1) :
        re != 0 && num == 2 ? genrey3(re, 1) :
        re != 0 && num == 3 ? genrey3(re, 0) :
        current === '' && num == 1 ? changePage(1) :
        current === '' && num == 2 ? changePage2(1) :
        current === '' && num == 3 ? changePage2(0) :
        num == 1 ? searchPress2(current, 1) :
        num == 2 ? searchPress3(current, 1) :
        num == 3 ? searchPress3(current, 0) : prevB()
      } 

     return (
       <div className="App">
      <header className="App-header">
        <Navbar>
          <GenreChoose>
            <div class="absolute right-10 top-12 z-10 mt-2 h-96 overflow-scroll overflow-x-hidden w-48 origin-top-right rounded-md bg-white py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none" role="menu" aria-orientation="vertical" aria-labelledby="user-menu-button" tabindex="-1">
                  <span><p className='text-block px-4 py-2 text-sm text-gray-900'>Find By Genres</p></span>
                  <a href="#" onClick={()=> genrey(28)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Action</a>
                  <a href="#" onClick={()=> genrey(12)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Adventure</a>
                  <a href="#" onClick={()=> genrey(16)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Animation</a>
                  <a href="#" onClick={()=> genrey(35)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Comedy</a>
                  <a href="#" onClick={()=> genrey(80)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Crime</a>
                  <a href="#" onClick={()=> genrey(99)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Documentary</a>
                  <a href="#" onClick={()=> genrey(18)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Drama</a>
                  <a href="#" onClick={()=> genrey(10751)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Family</a>
                  <a href="#" onClick={()=> genrey(14)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Fantasy</a>
                  <a href="#" onClick={()=> genrey(36)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">History</a>
                  <a href="#" onClick={()=> genrey(27)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Horror</a>
                  <a href="#" onClick={()=> genrey(10402)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Music</a>
                  <a href="#" onClick={()=> genrey(9648)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Mystery</a>
                  <a href="#" onClick={()=> genrey(10479)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Romance</a>
                  <a href="#" onClick={()=> genrey(878)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Sci-fi</a>
                  <a href="#" onClick={()=> genrey(10770)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Tv-Movie</a>
                  <a href="#" onClick={()=> genrey(53)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Thriller</a>
                  <a href="#" onClick={()=> genrey(10752)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">War</a>
                  <a href="#" onClick={()=> genrey(37)} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Western</a>
              </div>
          </GenreChoose>
          <NavItem>
            <div class="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none" role="menu" aria-orientation="vertical" aria-labelledby="user-menu-button" tabindex="-1">
                <a href='#' onClick={()=> dialog()} className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-700 hover:text-white " role="menuitem" tabindex="-1" id="user-menu-item-0">Your Profile</a>
                <a  onClick={()=> dialog()} href='#' className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-1">Settings</a>
                <a href="#" onClick={()=> usure()} className="block px-4 py-2 text-sm text-gray-700  hover:bg-gray-700 hover:text-white" role="menuitem" tabindex="-1" id="user-menu-item-2">Sign out</a>
            </div>
          </NavItem>
        </Navbar>
        <h1 className='text-4xl sm:text-5xl/8 m-8 tracking-wider mt-8'>MOVIE-MANIA</h1>
          <form className='flex h-[50px] w-[400px] sm:h-[60px] sm:w-[450px]' id='up'>
            <input placeholder='cari film kesayangan' className='Movie-search' id='searchBar' ref={barRef}></input>
            <button onClick={searchPress} className='rounded-full bg-gray-400 w-[60px] ml-3 hover:brightness-125 active:brightness-50 justify-center align-middle flex'><div className='mt-4'><AiOutlineSearch /></div></button>
          </form>
          <div className='flex justify-center w-full mt-10 sm:mt-6'>
            <h1 id='title' className='mx-auto place-self-start'>POPULAR MOVIES</h1>
            <h5 className='mx-auto text-gray-500 top-[230px]'>Page {num}/3 | Results: {popularMovies?.length}</h5>
          </div>
        <div className='wrapper-category w-full flex'>
          <div className='category relative w-5/12 left-28 text-start mb-9'>
        <dialog id='details' className='backdrop:bg-[rgba(0,0,0,0.5)] w-[1000px] h-[700px] bg-slate-700 rounded-[20px] text-left overflow-x-hidden'>
            <img className='w-full border-t-md' src={`${process.env.REACT_APP_POSTIMGURL}/${movie?.backdrop_path}`} alt={movie?.title} onError={({target}) => target.src = 'https://placehold.co/1000x555?text=NOT+FOUND'}></img>
            <div className='dePad absolute bg-gradient-to-b from-transparent to-black top-0 w-full'>
                <div className='absolute bottom-4 left-4 sm:left-8'>
                  <h1 className=' text-white text-2xl sm:text-5xl font-bold shadow-md truncate'>{movie?.title}</h1>
                  <p className='mt-2 text-slate-300 text-sm'>{movie?.original_title}</p>
                  <p className='text-slate-300 text-sm bg-slate-800 w-[38.3px] rounded-sm overflow-hidden'>⭐{movie?.vote_average}</p>
                  <p className='text-slate-300 text-sm'>{movie?.release_date}</p>
                  <Genres genre={genre }/>
                  <p className='text-slate-300 text-sm'>{movie?.original_language}</p>
                </div>
            </div>
               <hr className='mt-4 w-[97%] mx-auto'/>
              <p className='block mt-4 text-slate-300 text-sm mx-4 min-h-[400px] sm:min-h-[200px]'>{movie?.overview}</p> 
              <hr className='mb-4 w-[97%] mx-auto'/>
            <button onClick={() => unPop()}className='absolute text-slate-300 right-2 drop-shadow-md top-2 rounded-md bg-gray-600  hover:bg-gray-400 p-1'><AiOutlineClose /></button>
            <Youtube 
              className='w-full h-2/4 sm:h-full'
              opts={{
                width: "100%",
                height: "100%"
              }}
              videoId={vids}
            />
        </dialog>  
            <dialog id='modal' className='backdrop:bg-[rgba(0,0,0,0.5)] w-[400px] h-[500px] bg-slate-700 rounded-[20px] p-3 text-left'>
              <h1 className='text-white'>#user108394</h1>
              <p className='text-slate-300 text-sm mt-3'>email: user44@gmail.com</p>
              <p className='text-slate-300 text-sm mt-3'>password: ***********</p>
              <p className='text-slate-300 text-sm mt-3'>gender: ♂️</p>
              <p className='text-slate-300 text-sm mt-3'>country: Indonesia</p>
              <img src='https://placehold.co/600x600' className='rounded-full w-[120px] h-[120px] absolute left-[140px] bottom-[165px] hover:ring-2 hover:ring-white ring-offset-1'></img>
              <button onClick={() => unDialog()}className='absolute text-slate-300 right-2 drop-shadow-md top-2 rounded-md bg-gray-600  hover:bg-gray-400 p-1'><AiOutlineClose /></button>
            </dialog>
            <dialog id='usure' className='backdrop:bg-[rgba(0,0,0,0.5)] w-[400px] h-[160px] bg-slate-700 rounded-[20px] p-3 text-left'>
              <h1 className='text-white'>Are you sure?</h1>
              <button onClick={() => sure()} className='bg-gray-900 text-white hover:bg-gray-800 w-[120px] h-[50px] rounded-md absolute left-[150px] bottom-5'>Sign out</button>
              <button onClick={() => sure()}className='absolute text-slate-300 right-2 drop-shadow-md top-2 rounded-md bg-gray-600  hover:bg-gray-400 p-1'><AiOutlineClose /></button>
            </dialog>
          </div>
        </div>
        <div className="Movie-container">
          <PopularMovieList />
        </div>
      </header>
      <footer className='footer bg-[#393f4a] h-[100px] sm:h-[150px]'>
        <div className='Page-change relative top-1/4 flex gap-3 align-middle justify-center'>
          <a href='#searchBar'>
            <button onClick={() => prevButton()} className='bg-gray-500 text-white p-2 rounded-md hover:text-zinc-400 hover:bg-gray-300'>PREV</button>               
          </a>
          <a href='#searchBar'>
            <button onClick={() => nextButton()} className='bg-gray-500 text-white p-2 rounded-md hover:text-zinc-400 hover:bg-gray-300'>NEXT</button>                                        
          </a>
            <h2 className='text-gray-500 top-2 relative'>Page {num}/3 </h2>
        </div>
      </footer>
      <footer className="footer footer-center p-4 bg-base-300 text-base-content bg-gradient-to-br from-yellow-600 to-stone-400">
                <div>
                    <p>Copyright © 2023 - All right preserved</p>
                </div>
            </footer>
    </div>

  );
}